.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* padding: 3rem 4rem; */
}

.navbar-links {
    display: flex;
    flex: 1;
    justify-content: flex-end;
    align-items: center;
}

.navbar-links-container {
   display: flex;
   flex-direction: row;
   padding: 0 1rem;
}

.navbar-logo {
    font-weight: 400;
    font-size: 36px;
    line-height: 36px;
    color: #4B82ED;;
}

.navbar-links-container a,
.navbar-menu-container a{
    font-family: 'Outfit';
    font-weight: 500;
    font-size: 24px;
    line-height: 50px;
    color: #fff; 
    margin: 0 1.5rem;
}

.navbar-contact button,
.navbar-menu-container button{  
    font-family: 'Outfit';
    font-weight: 500;
    font-size: 24px;
    line-height: 100%;
    color: #FFFFFF;
    width: 182px;
    height: 62px;
    background: linear-gradient(139.67deg, #4378FF 26.6%, rgba(3, 85, 209, 0.61) 158.16%);
    border-radius: 7px;
    border: none;
    cursor: pointer;
}

.navbar-menu {
    display: none;
    margin-left: 1rem;
    position: relative;
}

.navbar-menu svg {
    cursor: pointer;
}

.navbar-menu-container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    position: absolute;
    background:  rgb(11, 17, 22);
    /* background: #4378FF; */
    padding: 2rem;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);;
}

.navbar-menu-container p {
    margin: 1rem 0;
}

@media screen and (max-width:1050px) {
    .navbar-links-container {
        display: none;
    }

    .navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 550px) {

    .navbar-contact {
        display: none;
    }

    .navbar-menu-contact {
        display: none;
    }

    .navbar-menu-container {
        top: 20px;
        display: flex;
    }
}