.deals__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;
    margin-top: 4rem;
}

.deals-heading {
    text-align: center;
    margin-top: 2rem;
}

.deals-title {
    font-family: 'Outfit';
    font-weight: 600;
    font-size: 64px;
    line-height: 150%;
    color:#4B82ED;
}

.deals-title2 {
    margin-left: 0.7rem;
    font-family: 'Outfit';
    font-weight: 600;
    font-size: 64px;
    line-height: 150%;
    color: #fff;   
}

.deals__item {
    background: var(--color-primary-variant);
    padding: 1.3rem;
    border: 1px solid transparent;
    border-radius: 2rem;
    transition: var(--transition);
}

.deals__item:hover {
    border-color: var(--color-primary-variant);
    background: transparent;
}

.deals__item-image {
    border-radius: 1.5rem;
    overflow: hidden;
}

.deals__item h3 {
    margin: 1rem 0 1rem;
}

.deals__item-cta {
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
}

        /* Media Queries */

 @media screen and (max-width: 1024px) {
    .deals__container {
        grid-template-columns: 1fr 1fr;
        gap: 1.7rem;
    }                  
}
        
@media screen and (max-width: 650px) {
    .deals__container {
        grid-template-columns: 1fr;
        gap: 1.2rem;
    }  
    
}