.header-image {
    display: flex;
    justify-content: center;
    width: 100%;
    position: relative;
}

.header-image img {
    width: 80vw;
    height: 60vh;
    opacity: 0.6;
}

.text {
    position: absolute;
    font-size: 50px;
}

.header-call {
    display: flex;
    justify-content: center;
}

.header-call button{
    padding: 35px 75px;
    border-radius: 7px;
    font-family: 'Outfit';
    font-weight: 500;
    font-size: 24px;
    line-height: 100%;
    display: flex;
    outline: none;
    color: #FFFFFF;
    margin-top: 2rem;
}