.brands {
    display: flex;
    flex-direction: column;
    margin-top: 6rem;
}

.brands-heading {
    text-align: center;
    margin-top: 2rem;
}

.brands-title {
    font-family: 'Outfit';
    font-weight: 600;
    font-size: 64px;
    line-height: 150%;
    color:#4B82ED;
}

.brands-title2 {
    margin-left: 0.7rem;
    font-family: 'Outfit';
    font-weight: 600;
    font-size: 64px;
    line-height: 150%;
    color: #fff;   
}

.brand-container {
    display: flex;
    flex-direction: column;
    /* width: 200px;
    height: 150px; */
    padding: 1.5rem;
    background: white;
    margin: 2rem auto;
    box-shadow: 0px 4px 24px 4px rgba(0, 0, 0, 0.06);
    border-radius: 8px;
}

.brand-img {
    width: 100%;
    height: 100%;
}

.brand-img img {
    width: 100px;
}
