.sub-footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background: #4db5ff;
}

.sub-heading {
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.sub-heading h2 {
    font-family: 'Euclid Circular A';
    font-weight: 500;
    font-size: 32px;
    line-height: 55px;
    color: #FFFFFF;
}

.sub-heading p {
    font-family: 'Euclid Circular A';
    font-weight: 300;
    font-size: 24px;
    line-height: 55px;
    color: #C9C9C9;
}


.sub-heading input {
    width: 266.63px;
    height: 60px;
    background: #FFFFFF;
    border-radius: 12px;
    text-align: center;
}

.sub-heading button {
    width: 144px;
    height: 60px;
    border-radius: 15px;
    margin-left: 1rem;
    font-family: 'outfit';
    font-weight: 500;
    font-size: 20px;
}

.sub-socials {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sub-socials img{
    display: inline-block;
    margin: 1rem;
    /* background: #3366e6;   */
    /* border: 9px solid #3366e6; */
    border-radius: 100px;
    width: 52px;
    height: 52px;
    filter: drop-shadow(0px 4px 4px rgba(120, 173, 22, 0.4));
}

.sub-socials p{
    font-family: 'Euclid Circular A';
    font-weight: 300;
    font-size: 24px;
    line-height: 55px;  
    color: #FFFFFF;
}

.sub-copyright {
    font-family: 'Euclid Circular A';
    font-weight: 400;
    font-size: 18px;
    line-height: 55px;
    color: #fff;
    margin-right: .7rem;
}

@media screen and (max-width: 1440px) {
    .sub-footer div {
        margin: 1rem 2rem;
    }
}

@media screen and (max-width: 780px) {

    .sub-footer {
        padding: 4rem;
    }
    .sub-footer div {
        margin: 1rem 0;
    }

    .sub-footer {
        flex-direction: column;
    }

}

@media screen and (max-width: 490px) {

    .sub-footer {
        padding: 4rem 2rem;
        min-width: 600px
    }

    .sub-input {
        display: flex;
    }

    .sub-footer {
        display: flex;
        align-items: flex-start;
    }

    .sub-copyright {
        font-size: 24px;
        line-height: 55px;
    }

    .sub-socials {
        display: flex;
        flex-direction: row;
    }
}
